.button {
    width: 132px;
    height: 30px;
    border-radius: 3px;
    padding: 5px 24px;
    border: 0;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 14px;
}

.disabled {
    background-color: #82a7cc;
    color: #ffffff;
}

.secondary {
    background-color: white;
    color: #0066CC;
    border: 2px solid #0066CC;
}

.default {
    background-color: #0066CC;
    color: white;
}

.red {
    background-color: #CC1439;
    color: white;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
