.message {
    width: 558px;
    height: auto;
    position: relative;
    margin-bottom: 2%;
    align-self: flex-end;
}

.imageAndText {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.imageAndText img {
    width: 48px;
    height: 48px;

}

.text {
    background-color: #0066CC;
    border-radius: 0.75rem 0.75rem 0 0.75rem;
    width: 495px;
    height: 56px;
    text-align: right;
    position: relative;
    padding: 5px;
}

.text:after {
    position: absolute;
    content: '';
    bottom: 0;
    right: -8px;
    border: 10px solid transparent;
    border-bottom: 10px solid #0066CC;

}

.name {
    font-size: 18px;
    font-weight: 600;
    color: white;
    margin-bottom: 2px;
}

.messageText {
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-weight: 300;
}

.time {
    font-size: 10px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    right: 11px;
}