.friendMessage {
    width: 558px;
    height: auto;
    position: relative;
    margin-bottom: 2%;
}

.friendImageAndText {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    color: black;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    background-color: #F5F7FB;
    border-radius: 0.75rem 0.75rem 0.75rem 0;
    width: 495px;
    height: auto;
    position: relative;
    padding: 5px;
}

.friendText:after {
    position: absolute;
    content: '';
    bottom: 0;
    left: -8px;
    border: 10px solid transparent;
    border-bottom: 10px solid #F5F7FB;
}

.friendName {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 2px;
}

.friendMessageText {
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    padding-bottom: 2px;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 11px;
}
