.sidebar {
    width: 22%;
    height: 100vh;
    position: fixed;
    padding: 10px;
    transform: translateX(-122%);
    transition: all 1s ease;
    background-color: white;
    z-index: 50;
}

.sidebar.open {
    transform: translateX(0);
}

.background {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.25);
    position: absolute;
    z-index: 25;
}

.nav {
    display: flex;
    flex-direction: column;
}

.nav a {
    color: black;
    text-decoration: none;
    margin-top: 45px;
    font-size: 22px;
    margin-left: 15%;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.nav a:hover {
    color: #58ACFF;
    text-decoration: none;
}


.close {
background-color: white;
    border:white;
    float: right;
}

